@keyframes fadeUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

body {
  background-image: url('img/bg2.jpg');
  background-size: cover;
  background-position: center;
  margin: 0;
  font-family: 'Lato', sans-serif;
  height: 100vh;
}

.heading-container {
  text-align: center;
  padding-top: 70px;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  opacity: 0;
  animation: fadeUp 1s ease-out forwards;
}

.heading-heading {
  font-family: "Righteous", sans-serif;
  opacity: 0;
  animation: fadeUp 1s ease-out forwards;
  animation-delay: 0.2s;
}

.card-container {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-grow: 1;
  min-height: 100vh;
  padding-top: 10px;
  margin-top: -60px;
  opacity: 0;
  animation: fadeUp 1s ease-out forwards;
  animation-delay: 0.5s;
}

.card {
  position: absolute;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.4);
}

.card-large {
  width: 700px;
  height: 550px;
  background: linear-gradient(180deg, #ff3333, #4b0082, #00008b, #000000);
  padding: 20px 120px 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.5);
}

.card-small {
  width: 620px;
  height: 470px;
  background: linear-gradient(180deg, #ff3333, #4b0082, #00008b, #000000);
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px 100px 40px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.5);
}

.contact-form {
  background: transparent;
  width: 100%;
  height: 100%;
  padding: 10px 20px 40px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: white;
  box-sizing: border-box;
}

.contact-form h2 {
  margin-bottom: 20px;
  font-family: "Righteous", sans-serif;
  font-size: 28px;
}

.input-group {
  margin-bottom: 20px;
  width: 90%;
}

.input-group input,
.input-group textarea {
  width: 100%;
  background: transparent;
  border: none;
  border-bottom: 1px solid white;
  padding: 15px;
  color: white;
  outline: none;
  font-size: 16px;
  font-family: "Lato", sans-serif;
  box-sizing: border-box;
  position: relative;
}

.input-group input::placeholder,
.input-group textarea::placeholder {
  color: grey;
  font-family: "Lato", sans-serif;
  position: absolute;
  top: 55%;
  left: 5px;
  transform: translateY(-50%);
}

.input-group textarea {
  resize: none;
  height: 50px;
}

.submit-button {
  background: transparent;
  border: 1px solid white;
  padding: 12px 50px;
  color: white;
  font-size: 16px;
  cursor: pointer;
  font-weight: 600;
  font-family: "Lato", sans-serif;
  transition: background 0.3s, color 0.3s;
}

.submit-button:hover {
  background: rgba(128, 128, 128, 0.5);
  color: white;
}

.success-message {
  font-size: 20px;
  color: #32cd32;
  background: rgba(0, 0, 0, 0.7);
  padding: 20px;
  border-radius: 10px;
  text-align: center;
  margin-top: 20px;
  text-shadow: 0 0 10px #32cd32, 0 0 20px #32cd32, 0 0 30px #32cd32, 0 0 40px #32cd32;
}

.submit-button .arrow {
  margin-left: 20px;
}
